  setDefaultValue() {
    this.currentStep = {
      titleStep: PersonalInfoStep.readContract,
      step: PersonalInfoStep.readContract,
      percent: PersonalInfoStepPercent.step1,
      jumpToStep: PersonalInfoStep.readContract,
    };
  }

  /**
   * 做完步驟之後要設定目前完成的步驟
   * @param titleStep 完成大步
   * @param step 步驟
   * @param jumpToStep 移動至此步
   */
  setFinishStep(titleStep: PersonalInfoStep, step: number, jumpToStep: PersonalInfoStep) {
    this.currentStep.titleStep = titleStep;
    this.currentStep.step = step;
    this.currentStep.percent = this.getPercent(titleStep, step);
    this.currentStep.jumpToStep = jumpToStep;
    this.showTitleStep(titleStep);
  }

  next(step: number) {
    this.currentStep.jumpToStep = step;
    this.currentStep.titleStep = step;
    this.currentStep.percent = this.getPercent(step);
  }

  /**
   * 跳步驟啦~~~
   * @param jumpToStep 前進至哪一個大步
   */
  jumpToStep(jumpToStep: PersonalInfoStep) {
    this.currentStep.jumpToStep = jumpToStep;
  }

