  showTitleStep(titleStep: PersonalInfoStep) {

  }

  getPercent(titleStep: PersonalInfoStep, step?: number) {
    switch (titleStep) {
      // 基本資料填寫
      case PersonalInfoStep.readContract:
        return PersonalInfoStepPercent.step1;

      // 推薦人及應徵職位
      case PersonalInfoStep.fillPersonalBrief:
        return PersonalInfoStepPercent.step2;
      // 推薦人及應徵職位
      case PersonalInfoStep.uploadDoc:
        return PersonalInfoStepPercent.step3;
      // 推薦人及應徵職位
      case PersonalInfoStep.finish:
        return PersonalInfoStepPercent.step4;
      default:
        return 0;
    }
  }
