import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { MessageService } from 'merc-lib';
import { NzUploadFile, NzUploadXHRArgs } from 'ng-zorro-antd/upload';
import { Observable, Observer, Subscription } from 'rxjs';
import { SharedService } from '../../../shared/shared.service';
import { DEVICE } from '../../../../../../tc-lib/src/lib/const/device.const';
import { ICON_PATH } from '../../const/icon-path.const';
import { Education } from '../enums/add-personal-info.enum';

@Component({
  selector: 'app-upload-doc',
  templateUrl: './upload-doc.component.html',
  styleUrls: ['./upload-doc.component.scss']
})
export class UploadDocComponent implements OnInit, OnChanges {
  @Input() basicData!: any;
  get ICON_PATH() { return ICON_PATH; }
  /**
   * 身分證
   */
  idCardList: NzUploadFile[] = [];
  passbookList: NzUploadFile[] = [];
  fileList: any[] = [];
  Education = Education;
  education: Education = this.basicData?.eduLoc;
  DEVICE = DEVICE;
  isConfirm = false;
  constructor(
    public sharedService: SharedService,
    private messageService: MessageService,
    
  ) { }

  ngOnInit(): void {
  }
  ngOnChanges(): void  {
    this.education = this.basicData.eduLoc;
  }
  /**
   * 處理上傳前的檔案
   * @param file 
   */
  beforeUpload = (file: NzUploadFile): boolean => {
    this.fileList = [file];
    console.log(this.idCardList);

    return false;
  }
  /**
   * 處理上傳前的檔案
   * @param file 
   */
  reUpload = (file: NzUploadFile, _fileList: NzUploadFile[]) =>
    new Observable((observer: Observer<boolean>) => {
      console.log(file);

      this.messageService.confirmModal('重新上傳後將覆蓋先前上傳檔案，您確認要重新上傳嗎？').afterClose.subscribe((res) => {
        console.log(res === 'confirm');

        observer.next(res === 'confirm');
      });
      observer.complete();
    });
  /**
   * TODO: for demo
   */
  onEducationChange(key: Education): void {
    this.education = key;
  }
  handleCustomerUpload = (item: NzUploadXHRArgs) => {
    console.log(item.postFile);

    return new Subscription();
  }
}
