import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-read-contract',
  templateUrl: './read-contract.component.html',
  styleUrls: ['./read-contract.component.scss']
})
export class ReadContractComponent implements OnInit {
  isReadDoc1 = false;
  isReadDoc2 = false;
  /**
   * 我已閱讀並同意上述條款
   */
  isRead = false;
  // readForm!: FormGroup;
  constructor() { }

  ngOnInit(): void {
  }
  /**
   * TODO: 不知道要不要擋滑動置底，先寫起來放
   * @param event 
   * @param doc 
   */
  detectBottom(event: boolean, doc: string): void {
    if (doc === 'doc1') {
      this.isReadDoc1 = event;
    }
    if (doc === 'doc2') {
      this.isReadDoc2 = event;
    }
    // console.log('isReadDoc1', this.isReadDoc1, 'isReadDoc2', this.isReadDoc2);
  }
}
