import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AccountService } from '../../../core/services/account.service';
import { PermissionService } from '../../../core/services/permission.service';
import { SharedService } from '../../../shared/shared.service';
import { DEVICE } from '../../../../../../tc-lib/src/lib/const/device.const';
import { ICON_PATH } from '../../const/icon-path.const';
import { PersonalInfoStep, PersonalInfoStepPercent } from '../enums/add-personal-info.enum';
import { StepStructure } from '../enums/step-structure.enum';
import { Steps } from '../interfaces/steps.interfaces';
import { InfoStepService } from './info-step.service';


@Component({
  selector: 'app-info-step',
  templateUrl: './info-step.component.html',
  styleUrls: ['./info-step.component.scss']
})
export class InfoStepComponent implements OnInit {
  @Output() readonly indexChange = new EventEmitter<number>();

  /**
   * 取得現在步驟
   */
  get currentStep() { return this.stepService.currentStep; }

  /**
   * 圖片路徑
   */
  get ICON_PATH() { return ICON_PATH; }

  /**
   * 計算步驟條寬度
   */
  get barWidth() {
    return `${100 * this.currentStep.percent}%`;

  }

  /**
   * 跑跑人跑步距離
   */
  get runDistance() {
    let percent = 100 * this.currentStep.percent + 13;
    if (this.currentStep.titleStep === PersonalInfoStep.finish) {
      percent = percent + 4;
    }
    if (this.sharedService.userDevice === DEVICE.MOBILE) {
      return `${percent}%`;
    } else {
      return `${percent + 3}%`;

    }
  }

  get PersonalInfoStep() { return PersonalInfoStep; }

  /**
   * 跑跑人性別
   * - 1: 男
   * - 2: 女
   */
  gender = this.stepService.getGender();

  steps: Steps[] = this.stepService.steps;

  constructor(
    public stepService: InfoStepService,
    public permissionService: PermissionService,
    private accountService: AccountService,
    private sharedService: SharedService
  ) { }

  ngOnInit(): void {
    // console.log(this.gender);
    // if (this.permissionService.isReadonly) {
    //   this.forPreview();
    //   console.log('isReadonly', this.permissionService.isReadonly);
    //   return;
    // }
    this.initFillInPage();
  }

  /**
   * 判斷目前進度是第幾步驟
   * - 有完成的頁面步驟要可點選
   */
  private initFillInPage() {

  }

  /**
   * 前往某大步
   * @param titleStep 大步驟
   */
  goToStep(titleStep: PersonalInfoStep) {
    this.indexChange.emit(titleStep);

    // this.stepService.jumpToStep(titleStep);
    console.log(titleStep);
  }

}
