import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { LoadingService, MessageService } from 'merc-lib';
import { RunningStepComponent } from 'projects/tc-lib/src/public-api';
import { ROUTING_PATH } from '../const/routing-path.const';
import { PersonalInfo, PersonalInfoStep } from './enums/add-personal-info.enum';
import { FillPersonalBriefComponent } from './fill-personal-brief/fill-personal-brief.component';
import { InfoStepService } from './info-step/info-step.service';
import { ReadContractComponent } from './read-contract/read-contract.component';


@Component({
  selector: 'app-add-personal-info',
  templateUrl: './add-personal-info.component.html',
  styleUrls: ['./add-personal-info.component.scss'],
  providers:[InfoStepService]
})

export class AddPersonalInfoComponent implements OnInit {
  @ViewChild('runStep') runStep?: RunningStepComponent;
  @ViewChild(FillPersonalBriefComponent) fillPersonalBrief!: FillPersonalBriefComponent;
  @ViewChild(ReadContractComponent) readContract!: ReadContractComponent;
  PersonalInfo = PersonalInfo;
  stepList: any[] = [
    { step: 1, stepTitle: '權益聲明暨個資告知', index: PersonalInfo.readContract },
    { step: 2, stepTitle: '填寫基本資料', index: PersonalInfo.fillPersonalBrief },
    { step: 3, stepTitle: '上傳證明文件', index: PersonalInfo.uploadDoc },
    { step: 4, stepTitle: '送出審核', index: PersonalInfo.finish },
  ];
  stepsName = ['權益聲明暨個資告知', '填寫基本資料', '上傳證明文件', '送出審核']
  stepIndex = 0;
  basicData: any;
  constructor(
    private messageService: MessageService,
    private router: Router,
    private infoStepService: InfoStepService
  ) { }

  ngOnInit(): void {
  }
  jumpToStep(step: any): void {
    if (step || step === 0) {
      this.infoStepService.next(step);
      this.stepIndex = step;
    }
    // this.stepIndex = step;
    // this.infoStepService.jumpToStep(this.stepIndex);
    // this.runStep?.next(this.stepIndex);
  }
  back(): void {
    if (this.stepIndex !== PersonalInfo.readContract) {
      this.stepIndex--;
      this.infoStepService.jumpToStep(this.stepIndex);
      // this.runStep?.goToStep(this.stepIndex);
    }
  }
  next(): void {
    const desc = `您已成功送出申請，市場訓練處/科同仁將於７個工作天完成審核！
    待審核通過後，您即可使用線上報名系統！
    如經審核發現您資料缺漏或不齊，將以e-mail通知您補件，請多加留意！`;
    switch (this.stepIndex) {
      case PersonalInfo.readContract:
        const isRead = this.readContract.isRead;
        if (isRead) {
          this.stepIndex++;
          this.infoStepService.next(this.stepIndex);
          // this.runStep?.next(this.stepIndex);
        } else {
          this.messageService.promptModal('請詳閱以上文件之內容，方可進行下一步');
        }
        break;
      case PersonalInfo.fillPersonalBrief:
        const isValidate = this.fillPersonalBrief.isValidate();
        if (isValidate) {
          const formValue = this.fillPersonalBrief?.validateForm.value;
          this.basicData = formValue;
          console.log(formValue);
          this.stepIndex++;
          this.infoStepService.next(this.stepIndex);
          // this.runStep?.next(this.stepIndex);
        }
        break;
      case PersonalInfo.uploadDoc:
        // this.runStep?.next(this.stepIndex);
        this.messageService.successModal(desc, { imgPath: './assets/image/icon/icon_success@3x.png', msgTitle: '您已送出申請' });
        break;
      case PersonalInfo.finish:

        break;
      default:
        break;
    }
  }
  saveAndBack(): void {
    this.messageService.confirmModal('確定儲存並離開此頁面嗎?').afterClose.subscribe((result) => {
      console.log(result);
      if (result) {
        this.router.navigate([ROUTING_PATH.HOME]);
      }
    })
  }
}
