import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { errorMsg, MessageService, SwiperDatePickerService, TaiwanDatePipe, validateIdCard, validateMobile } from 'merc-lib';
import * as moment from 'moment';
import { NzModalService } from 'ng-zorro-antd/modal';
import { CRUD_CONFIG } from '../../const/crud.const';
import { ICON_PATH } from '../../const/icon-path.const';
import { EduWarningModalComponent } from './edu-warning-modal/edu-warning-modal.component';
import { eduList, nationalityList, personInfo } from '../../../shared/mocks/mock';

@Component({
  selector: 'app-fill-personal-brief',
  templateUrl: './fill-personal-brief.component.html',
  styleUrls: ['./fill-personal-brief.component.scss']
})
export class FillPersonalBriefComponent implements OnInit {
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  // form error msg
  errorMsg = errorMsg;

  // 假資料 start
  nationalityList = nationalityList;
  eduList = eduList;
  // 假資料 end


  validateForm: FormGroup = this.fb.group({
    name: ['', Validators.required],
    enLastName: [''],
    enName: [''],
    birth: ['', Validators.required],
    sex: [{ value: '', disabled: true }, Validators.required],
    id: ['', [validateIdCard(), Validators.required]],
    nationality: ['', Validators.required],
    otherNationality: [{ value: '', disabled: true }],
    eduLoc: ['', Validators.required],
    education: ['', Validators.required],
    graduate: ['', Validators.required],
    mobile: ['', [validateMobile(), Validators.required]],
    email: ['', [Validators.email, Validators.required]],
    director: ['林阿嬌'],
    referee: ['王小明']
  });
  constructor(
    private fb: FormBuilder,
    private swiperDatePickerService: SwiperDatePickerService,
    private taiwanDatePipe: TaiwanDatePipe,
    private modalService: NzModalService,
    private messageService: MessageService
  ) { }


  ngOnInit(): void {
    // if (this.type === CRUD_CONFIG.CREATE) {
    // } else {
    //   // read
    //   this.validateForm.patchValue(personInfo);
    //   this.validateForm.disable();
    // }
  }

  ngOnDestroy(){
    console.log('ngOnDestroy');
    
  }

  /** 送出表單 */
  isValidate(): boolean {
    const controls = this.validateForm.controls;
    for (const i in controls) {
      if (controls.hasOwnProperty(i)) {
        controls[i].markAsDirty();
        controls[i].updateValueAndValidity();
      }
    }
    console.log(this.validateForm.controls);
    return this.validateForm.valid;
  }

  /**
   * 選擇學歷提示訊息
   * @edu 國內|國外|在學
   */
  handleEdu(edu: string): void {
    if (edu === 'A' || edu === 'B' || edu === 'E') {
      this.modalService.create({
        nzContent: EduWarningModalComponent,
        nzWidth: 600,
        nzMaskClosable: false,
        nzFooter: null,
        nzBodyStyle: { padding: '12px 24px' },
        nzComponentParams: { edu }
      });
    }

    // 選擇中學(含)以下
    if (edu === '5') {
      this.messageService.errorModal('您不符合參加人身保險業務員資格測驗資格，請確認是否正確！', { imgPath: './assets/image/icon/icon_alert@3x.png' });
    }
  }

  onPickNationality(option: string): void {
    // 國籍選擇其他，開放下拉
    if (option === 'D') {
      this.validateForm.get('otherNationality')?.enable();
      this.validateForm.get('otherNationality')?.setValidators([Validators.required]);
    } else {
      this.validateForm.get('otherNationality')?.disable();
    }
  }

  /** 從身分證判定性別 */
  onCheckSex(id: string): void {
    if (this.validateForm.get('id')?.valid) {
      const sexDef = id.slice(1, 2) === '1' ? '男' : '女';
      this.validateForm.get('sex')?.patchValue(sexDef);
    }
  }


  /**
   * 只能輸入英文且大寫
   * @ev 事件
   * @control formControlName
   */
  upperEnglishOnly(ev: any, control: string): void {
    // 先全轉大寫
    const upper = (ev.target.value as string).toUpperCase();
    const chars = upper.split('');
    const lastChar = chars.pop() as string;
    // 檢查最新輸入的是否為英文
    const onlyEnglish = /[A-Z]/.test(lastChar);

    if (onlyEnglish) {
      this.validateForm.get(control)?.patchValue(upper);
    } else {
      this.validateForm.get(control)?.patchValue(chars.join(''));
    }
  }

}
